(16px);\n    -ms-transform: translateX(16px);\n    transform: translateX(16px);\n}\n\n/* Rounded sliders */\n.slider.round[data-v-6e8cc093] {\n    border-radius: 12px;\n}\n.slider.round[data-v-6e8cc093]:before {\n    border-radius: 50%;\n}\n.checkbox[data-v-6e8cc093]{\n    display: flex;\n    flex-direction: row;\n    align-items: center;\n    padding: 0px;\n    gap: 10px;\n    width: 229px;\n    height: 20px;\n    flex: none;\n    order: 2;\n    flex-grow: 0;\n}\n.margin[data-v-6e8cc093] {\n    margin-left: 30px;\n}\n.select[data-v-6e8cc093]:invalid {\n    color: gray;\n}\n.option[data-v-6e8cc093] {\n    color: black;\n}\n.checkinInputs[data-v-6e8cc093] {\n    border: 1px solid #A1A1A1;\n    border-radius: 4px;\n    opacity: 1;\n    background: #FFFFFF 0% 0% no-repeat padding-box;\n}\n.checkinTextarea[data-v-6e8cc093] {\n    border: 1px solid #A1A1A1;\n    border-radius: 4px;\n    opacity: 1;\n    background: #FFFFFF 0% 0% no-repeat padding-box;\n    height: 120px;\n    resize: none;\n}\n.checkinBtnText[data-v-6e8cc093] {\n    font-style: normal;\n    font-weight: 500;\n    font-size: 14px;\n    font-family: 'Roboto';\n    letter-spacing: 0.02px;\n    opacity: 1;\n}\n.checkinBtn[data-v-6e8cc093] {\n    background-color: #151233;\n    width: 104px;\n    height: 32px;\n    color: white;\n    border-radius: 4px;\n    opacity: 1;\n    margin-top: 22px;\n}\n.buttonGroupDiv[data-v-6e8cc093] {\n    height: 264px;\n    top: 92px;\n    display: flex;\n    margin-top: 32px;\n    flex-direction: column;\n    align-items: flex-start;\n    padding: 0px 20px;\n    gap: 20px;\n    width: 420px;\n    height: 264px;\n    flex: none;\n    order: 1;\n    flex-grow: 0;\n}\n.dialogContentDivs[data-v-6e8cc093] {\n    width: 570px;\n    font-family: 'Roboto';\n    font-style: normal;\n    font-weight: 400;\n    font-size: 15px;\n    line-height: 18px;\n    align-self: stretch;\n}\n.checkinLabels[data-v-6e8cc093] {\n    font-style: normal;\n    color: #151233;\n    font-weight: 500;\n    