mport { IConfig } from '../../../../../domain/infrastructure/IConfig';\nimport { IModel } from '../../../../../domain/model/models';\nimport { IIDEInputDefinitions } from '../../../../../domain/presentation/IIDEInputDefinitions';\nimport { IStudio } from '../../../../../domain/useCase/IStudio';\nimport { IUseCaseExecutor } from '../../../../../domain/useCase/IUseCaseExecutor';\nimport { IViewModel } from '../../../../../domain/viewModel/IViewModel';\nimport IDEInput from \"../../../components/application/IDEInput.vue\";\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst studio = container.resolve<IStudio>(IStudio);\nconst config = container.resolve<IConfig>(IConfig);\n\ninterface ISavePlus {\n    item: IModel;\n}\n\nconst props = defineProps<ISavePlus>();\nconst releaseVersion = ref<boolean>(false);\nconst isNameValid = ref<boolean>(false);\nconst isDescriptionValid = ref<boolean>(true);\nconst validations = ref<Record<\"name\" | \"description\", IIDEInputDefinitions[\"validations\"]>>({\n    name: { minAndMaxLength: { min: 2, max: 40 }},\n    description: { minAndMaxLength: { min: 2, max: 500 } },\n});\n\nconst checkin = ref(true);\nconst buttonName = computed(() => checkin.value ? \"SAVE CHANGES\" : \"SAVE\");\nconst version = ref<\"major\" | \"minor\" | \"\">(\"minor\");\nconst comment = ref({ short: \"\", long: '', hasLong: false });\nconst emit = defineEmits([\"close\"]);\nconst buttonDisabled = computed(() => !isNameValid.value || !isDescriptionValid.value || version.value =='');\n\nconst commentMaxLength = config.getValue(\"studio\").commentMaxLength;\n\nfunction handleVersion($event: any) {\n    releaseVersion.value = $event.target.checked;\n    if($event.target.checked){\n        version.value = \"major\";\n    }\n    else\n    {\n        version.value = \"minor\";\n    }\n}\n\nfunction save() {\n    executor.execute(async () => {\n        if (checkin.value) {\n            await studio.checkin(props.item, { shortComment: comment.value.short, version: version.value, longComment: comment.value.long });\n        } else {\n            await studio.save(props.item);\n        }\n        emit(\"close\");\n    }, { loading: true });\n}\n\nfunction discard(){\n    executor.execute(async () => {\n        await studio.undoCheckout(props.item);\n        emit(\"close\");\n    }, { loading: true });\n}\n\nfunction cancel() {\n    emit(\"close\");\n}\n\nfunction setNameValue(value: string) {\n    comment.value.short = value\n}\nfunction setNameValidationStatus(isValid: boolean) {\n    isNameValid.value = isValid\n}\n\nfunction setDescriptionValue(value: string) {\n\n    comment.value.long = value\n}\n\nfunction setDescriptionValidationStatus(isValid: boolean) {\n    isDescriptionValid.value = isValid\n}\n\n</script>\n<template>\n    <div>\n\n        <template v-if=\"checkin\">\n            \n            <div class=\"dialogContentDiv\">\n                <div class=\"check-in-info-div\">\n                    <span id=\"release-info-up\" class=\"release-info-up\">\n                        <i class=\"mdi mdi-information release-info-icon-up\" /> \n                        <span class=\"check-in-definition-text\">\n                            <span style=\"font-weight:500\">Stop working</span> saves your changes and allows others to work on this application.\n                        </span>\n                    </span>\n                </div>\n\n                <div class=\"dialogContentDivs\">\n                    <IDEInput :isRequired=\"true\" @getValue=\"setNameValue\" @validateStatus=\"setNameValidationStatus\"\n                        :placeholder=\"'Summarise your new version.'\" :type=\"'text'\" info-text=\"Summary\" :disableRequiredIcon=\"false\"\n                        :validations=\"validations.name\" />\n                </div>\n\n                <div class=\"dialogContentDivs\">\n                    <IDEInput :isRequired=\"false\" @getValue=\"setDescriptionValue\" :disableRequiredIcon=\"true\"\n                        @validateStatus=\"setDescriptionValidationStatus\" :placeholder=\"'Describe your new version.'\"\n                        :type=\"'textarea'\" info-text=\"Description (Optional)\" :validations=\"validations.description\" />\n                </div>\n                <div class=\"checkbox\">\n                    <label class=\"switch\" for=\"release-version\">\n                        <input id=\"release-version\" type=\"checkbox\" v-model=\"releaseVersion\"\n                            @change=\"handleVersion\">\n                        <span class=\"slider round\"></span>\n                    </label>\n                    <span class='release-version-text'>Ready to publish.</span>\n                    <span id=\"release-info\" class=\"release-info\">\n                        <i class=\"mdi mdi-information release-info-icon\" /> \n                        <span class=\"release-info-tooltip\">\n                            If you deselect this option, changes will be saved but\n                            will not be applied to publish of your application.\n                        </span>\n                    </span>\n                </div>\n            </div>\n\n        </template>\n        <div class=\"buttonGroup\">\n            <div type=\"button\" style=\"align-self: center;\" @click=\"discard()\">\n                <span class=\"discard-button-text\">Want To Discard Changes?</span>\n            </div>\n            <div class=\"right\">\n                <div type=\"button\" class=\"cancel-button\" @click=\"cancel()\">\n                    <span class=\"cancel-button-text\">Cancel</span>\n                </div>\n                <div type=\"button\" :class=\"!buttonDisabled? 'check-in-button' : 'check-in-button disabled'\" @click=\"save()\">\n                    <span class=\"check-in-button-text\" :style=\"!isNameValid? 'color: #757575;':'' \">{{ buttonName }}</span>\n                </